.full-width {
    display: flex;
    justify-content:center;
}

.docs-entry-content {
    width:100%;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.app-container,
.img-container {
    max-width: 600px;
}

.img-container {
    padding: 3rem 0 0 0;
}

/* *********************************** *
PLACEHOLDERS BEFORE DATA IS LOADED
* *********************************** */
.load_filler_container {
    width:100%;
    max-width: 700px;
}

.load_filler {
    width:100%;
    height: 45px;
}

.load_filler:nth-child(odd) {
    background-color: gainsboro;
}

.load_filler:nth-child(even) {
}

img {
    height:100%;
    width:100%;
}

/* *********************************** *
TABLE
* *********************************** */
.table-container {
    width:100%;
    margin: 2rem 0 4rem 0;
}

.table {
    width:100%;
    max-width: 700px;
    border-collapse: collapse;
    font-family: sans-serif;
    font-size: 0.85rem;
}

.table tr {
    height: 45px;
}

.table th {
    text-align: left;
}

.table td {
    border-bottom: 1pt black solid;
}

.table th {
    border-bottom: 2pt black solid;
}

.table th,
.table td {
    padding: 0 0 0 0.5rem;
}

/* .table tbody tr:nth-child(odd) {
    background-color: #F5F5F5;
} */

.table td.last_move {
    font-size: 0.75rem;
}

.table td.url a,
.table td.last_move {
    color: #696969;
}

.table th:first-of-type {
    width:5%
}

.table th:nth-of-type(4) {
    width:45%
}

/* *********************************** *
TABLE MOBILE
* *********************************** */
@media (max-width:600px) {
    .table th:nth-child(4),
    .table td:nth-child(4) {
        display: none;
    }
}


/* *********************************** *
STATUS CELL STYLING
* *********************************** */
.table .committee,
.table .vote,
.table .sign,
.table .veto,
.table .fail,
.table .pass,
.table .held {
    padding: 0;
    text-align: center;
    width: 30%;
    font-weight: bold;
}

.table .status {
    border: 1pt #C0C0C0 solid;
    border-radius: 5px;
    padding: 15px 25px;
}

.table .committee p {
    background-color: #F2ECDE;
    margin: 1.25rem 0;
}

.table .vote p {
    background-color: #8AA9DB;
    margin: 1.25rem 0;
}

.table .sign p {
    background-color: #F9D369;
    margin: 1.25rem 0;
}

.table .veto p {
    background-color: #F29D4F;
    margin: 1.25rem 0;
}

.table .fail p {
    background-color: #C92E28;
    margin: 1.25rem 0;
}

.table .pass p {
    background-color: #94C55F;
    margin: 1.25rem 0;
}

.table .held p {
    background-color: #EAF3E0;
    margin: 1.25rem 0;
}
